@import url(https://fonts.googleapis.com/css?family=Exo+2:300,400&display=swap);
:root {
  --background: rgb(40, 45, 50);
  --btn: rgb(10, 15, 20);
  --btn-hover: rgb(29, 36, 44);
  --btn-active: rgb(0, 0, 0);
  --black: rgb(0, 5, 10);
  --input-placeholder: rgb(112, 115, 117);
  --input-background: rgb(31, 34, 37);
  --white: rgb(210, 220, 230);
  --white-disabled: rgb(141, 145, 148);
  --fs16: 16px;
  --fs18: 18px;
  --fs20: 20px;
  --fs22: 22px;
  --fs24: 24px;
  --px80: 80px;
  --px50: 50px;
  --px60: 60px;
  --px30: 30px;
  --px20: 20px;
  --px10: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
  padding: 0 var(--px10);
  padding-top: 20px;
  padding-top: var(--px20);
}

body, input, button {
  font-family: 'Exo 2', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(40, 45, 50);
  background-color: var(--background);
  color: rgb(210, 220, 230);
  color: var(--white);
  font-size: 18px;
  font-size: var(--fs18);
}

input::-webkit-input-placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input:-ms-input-placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input::-ms-input-placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input::placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input {
  border: 1px solid rgb(112, 115, 117);
  border: 1px solid var(--input-placeholder);
  padding: 5px 10px;
  padding: 5px var(--px10);
  background-color: rgb(31, 34, 37);
  background-color: var(--input-background);
  border-radius: 2px;
}

ul {
  list-style: none;
}

button, .btn{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 200px;
  height: 50px;
  height: var(--px50);
  border: none;
  background-color: rgb(10, 15, 20);
  background-color: var(--btn);
  color: rgb(210, 220, 230);
  color: var(--white);
  font-size: 18px;
  font-size: var(--fs18);
  cursor: pointer;
  transition: .2s;
  border-radius: 2px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

button:hover, .btn:hover {
  background-color: rgb(29, 36, 44);
  background-color: var(--btn-hover);
}

button:active, .btn:active {
  background-color: rgb(0, 0, 0);
  background-color: var(--btn-active);
  border: none;
  box-shadow: none;
  outline: none;
}

@media (max-width: 720px) {
  :root {
    --fs16: 12px;
    --fs18: 14px;
    --fs20: 16px;
    --fs22: 18px;
    --fs24: 20px;
    --px80: 60px;
    --px60: 50px;
    --px50: 30px;
    --px30: 20px;
    --px20: 10px;
    --px10: 5px;
  }
}
.task-list {
	padding: 0 var(--px10);
	box-shadow: 0px 3px 30px -15px black;
}
.task-list-item {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: var(--px10) 0;
	padding-left: var(--px10);
	border-bottom: 1px solid var(--black);
}

.task-list-item:last-child {
	border-bottom: none;
}

.task-info {
	margin-left: auto;
	margin-right: var(--px10);
}

.task-info-item {
	font-size: var(--fs16);
	color: var(--white-disabled);
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}
.task-info-item p:first-child {
	margin-right: var(--px10);
}

.task-create-type-choise {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: var(--px20);
}

.task-title {
	font-size: var(--fs24);
	max-width: 80%;
	overflow-wrap: break-word;
}

.task-duration {
	display: flex;
	align-items: center;
	justify-content: space-around;
	font-size: var(--fs20);
}

.task-create-type-choise .btn {
	margin-left: 5px;
	width: auto;
	padding: 0 var(--px20);
	overflow: hidden;
	position: relative;
}

.delete-task {
	width: var(--px50);
	height: var(--px50);
	position: relative;
	padding: 0;
}

.delete-task img {
	position: absolute;
	height: 70%;
	opacity: 0.7;
	transition: .2s;
}

.delete-task:hover img  {
	height: 75%;
	opacity: 0.6;
}

.task-create-type-choise .btn img{
	position: absolute;
	height: 100%;
	opacity: .1;
	right: var(--px10);
}

.task-create-type-choise .btn:hover img {
	height: 150%;
	right: -5px;
	opacity: .05;
	transition: all .2s ease, -webkit-transform 10s linear;
	transition: all .2s ease, transform 10s linear;
	transition: all .2s ease, transform 10s linear, -webkit-transform 10s linear;
	-webkit-transform: rotateZ(1000deg);
	        transform: rotateZ(1000deg);
}

.addTask-wrap {
	box-shadow: 0px 3px 30px -15px black;
	padding: var(--px10);
	margin-bottom: var(--px20);
}

.task-create-title {
	font-size: var(--fs24);
	margin-bottom: var(--px20);
	width: 100%;
}

.task-create-btn {
	width: 100%;
	height: var(--px60);
	font-size: var(--fs22);
}

.task-create-time {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: var(--px20);
}

.task-create-time input {
	font-size: var(--fs16);
	margin: 0 var(--px10);
}

@media (max-width: 470px) { 
	.task-create-time {
		flex-direction: column;
		justify-content: space-around;
	}
}
@media (max-width: 370px) { 
	.task-create-type-choise {
		flex-direction: column;
		margin-bottom: 0;
	}
}
.salary-calc-wrap {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	align-items: center;
	justify-content: space-around;
	top: 0;
	left: 0;
	z-index: 15;
}

.salary-calc-close-btn {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-around;
	margin-left: auto;
	cursor: pointer;
}

.salary-calc-close-btn img {
	position: absolute;
	right: -65px;
	top: -65px;
	width: var(--px30);
	height: var(--px30);
	transition: .2s;
	opacity: .8;
}

.salary-calc-close-btn:hover img{
	opacity: 1;
}

.salary-calc-form {
	padding: var(--px30);
	background-color: var(--background);
}

.salary-rate-wrap {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	margin-bottom: var(--px30);
}

.salary-rate-wrap input {
	width: var(--px80);
	margin: 0 5px;
}

.salary-calc-option label{
	display: flex;
	align-items: center;
	flex-direction: row;
	margin-bottom: var(--px20);
}

.salary-calc-option label input {
	margin-right: var(--px10);
}

.salary-calc-date {
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: var(--px20);
}

.salary-calc-btn {
	margin: 0 auto;
	margin-bottom: var(--px20);
}

.salary-calc-result {
	font-size: var(--fs22);
	text-align: center;
}

@media (max-width: 720px) { 
	.salary-calc-close-btn img {
		right: -40px;
		top: -40px;
	}
}
.datePicker input{
	background-color: var(--background);
	cursor: pointer;
	transition: .2s;
}

.datePicker input:hover {
	background-color: var(--input-placeholder);
}

#datePicker{
	font-size: var(--fs24);
	border: none;
}

@media (max-width: 470px) {
	#datePicker {
		margin-bottom: 10px;
	}
}
.main-header {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 3px 30px -15px black;
	height: var(--px60);
	margin-bottom: var(--px20);
	position: relative;
	padding: 35px 0;
}

.salary-calc-show-btn {
	position: absolute;
	right: var(--px10);
}

@media (max-width: 720px) { 
	.main-header {
		justify-content: flex-start;
	}
}
@media (max-width: 470px) { 
	.salary-calc-show-btn {
		position: static;
	}
	.main-header {
		justify-content: center;
		flex-direction: column;
		height: auto;
		padding: 10px 0;
	}
}
